<template>
  <div>
    <LoHeader class="lo_header" @query="queryData" :FilterItems="FilterItems" title="组投注记录"></LoHeader>
    <mu-container ref="container" class="lo_content_box">
      <mu-load-more class="lo_content" :refreshing="refreshing" :loading="loading" style="width: 100%;"
                    @refresh="refresh" @load="load">
        <template v-for="item in tableData">
          <div>
            <div class="container">
              <div class="desc">

                <div class="title">
                  <div>
                    <span class="label">期号:</span>
                    <span class="title_l">{{item.StrPeriod}}</span>
                  </div>
                  <div>
                    <span class="label">时间:</span>
                    <span class="title_l">{{item.CreatedAt}}</span>
                  </div>
                </div>
                <div class="title">
                  <div>
                    <span class="label">流水号:</span>
                    <span class="title_l">{{item.BetSn}}</span>
                  </div>
                  <div>
                    <span class="label">状态:</span>
                    <span class="title_l">{{item.Status|BetStateName}}</span>
                  </div>

                </div>

                <div class="title">
                  <div>
                    <span class="label">用户名:</span>
                    <span class="title_l">{{item.UserName}}</span>
                  </div>
                  <div>
                    <span class="label">彩种:</span>
                    <span class="title_l">{{item.GameName}}</span>
                  </div>
                </div>

                <div class="title">
                  <div>
                    <span class="label">总投注:</span>
                    <span class="title_l">{{item.BetM}}</span>
                  </div>
                  <div>
                    <span class="label">赢得:</span>
                    <span class="title_l">{{item.Win}}</span>
                  </div>
                  <div class="btn">
                    <div class="btn_01" @click="openDetailDialog(item)">详情</div>
                  </div>
                </div>

                <div class="title">
                  <div>
                    <template v-for="betItem in item.ArrBetDetail">
                      <mu-chip>
                        {{ betItem }}
                      </mu-chip>
                    </template>

                  </div>
                </div>

              </div>
            </div>
          </div>
        </template>
      </mu-load-more>
    </mu-container>

    <mu-dialog width="360" transition="slide-bottom" fullscreen :open.sync="DetailData.showDetailDialog">
      <mu-appbar color="primary" title="投注明细">
        <mu-button slot="right" flat  @click="DetailData.showDetailDialog=false">
          关闭
        </mu-button>
      </mu-appbar>
      <div style="padding: 24px;">
        <div class="desc">
          <div class="title">
            <div>
              <span class="label">用户名:</span>
              <span class="title_1">{{DetailData.GroupUserName}}</span>
            </div>
            <div>
              <span class="label">彩种:</span>
              <span class="title_1">{{DetailData.GroupGameName}}</span>
            </div>
            <div>
              <span class="label">期号:</span>
              <span class="title_1">{{DetailData.GroupStrPeriod}}</span>
            </div>
          </div>
          <div class="title">
            <span class="label">开奖号码:</span>
            <span class="title_1">{{DetailData.GroupResultNums}}</span>
          </div>
        </div>
          <template v-for="item in DetailData.tableData">
            <div class="container">
              <div class="desc">
                <div class="title">
                  <div>
                    <span class="label">投注内容:</span>
                    <span class="title_1">{{item.OddsDes}}</span>
                  </div>
                  <div>
                    <span class="label">赔率:</span>
                    <span class="title_1">{{item.Odds}}</span>
                  </div>
                  <div>
                    <span class="label">状态:</span>
                    <span class="title_l">{{item.Status|BetStateName}}</span>
                  </div>
                </div>

                <div class="priceDiv">
                  <div>
                    <span class="label">金额：</span>
                    <span class="price">{{item.BetM}}</span>
                  </div>
                  <div>
                    <span class="label">赢得：</span>
                    <span class="price">{{item.Win}}</span>
                  </div>
                </div>
              </div>
            </div>
          </template>

      </div>
    </mu-dialog>

  </div>
</template>

<script>
import LoHeader from "../../components/LoHeader";
import {GetGroupBetListFirstId,GetGroupDetailBetList} from '../../api/Report';
export default {
  name: "GroupBetList",
  components: {
    LoHeader
  },
  data () {
    return {
      num: 10,
      refreshing: false,
      loading: false,

      FilterItems:[
        {label:'用户名:',Value:'',type:"text",placeholder:"用户名",icon:"",Name:"userName"},
        {label:'流水号:',Value:'',type:"text",placeholder:"流水号",icon:"",Name:"GroupBetSn"},
        {label:'期号:',Value:'',type:"text",placeholder:"期号",icon:"",Name:"Period"},
        {label:'彩种:',Value:0,type:"select",placeholder:"彩种",icon:"",Name:"gameType",options:this.Config.GameTypeOptions},
        {label:'投注状态:',Value:0,type:"select",placeholder:"投注状态",icon:"",Name:"state",options:this.Config.BetStateOptions},
        {label:'用户类型:',Value:0,type:"select",placeholder:"用户名",icon:"",Name:"userType",options:this.Config.UserTypeOptions},
        {label:'开始时间:',Value:'',type:"date",placeholder:"开始时间",icon:"",Name:"beginDay"},
        {label:'结束时间:',Value:'',type:"date",placeholder:"结束时间",icon:"",Name:"endDay"},
      ],
      PageTotal:0,
      tableData: [],
      GroupData:{},
      query: {
        Period: '',
        GroupBetSn: '',
        beginDay: '',
        endDay: '',
        state:0,
        userType:0,
        pageIndex: 1,
        pageSize: 10,
        gameType:0,
        FirstId:0,
      },

      DetailData:{
        showDetailDialog:false,
        DialogTitle:'投注明细',

        GroupUserName:'',
        GroupStrPeriod:'',
        GroupGameName:'',
        GroupResultNums:'',
        tableData:{
        }
      }
    }
  },
  methods: {
    refresh () {
      this.refreshing = true;
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.refreshing = false;
      });
    },
    load() {
      this.loading = true;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.loading = false;
      });
    },
    getData(fun){
      GetGroupBetListFirstId(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.query.FirstId==0) {
            this.tableData = obj.ListData;
          } else {
            this.tableData = this.tableData.concat(obj.ListData);          }
          this.GroupData = obj.GroupData;
          this.PageTotal = obj.PageTotal;
          this.query.FirstId = obj.GroupData.FirstId;

          fun(true);
          resolve();
        } else {
          this.$toast.error(msg);
          fun(false);
        }
      }).catch(error => {
        fun(false);
      });
    },
    openDetailDialog(item){
      this.DetailData.showDetailDialog = true;

      this.DetailData.GroupUserName = '';
      this.DetailData.GroupStrPeriod = '';
      this.DetailData.GroupGameName = '';
      this.DetailData.GroupResultNums = '';
      this.DetailData.tableData = {};

      GetGroupDetailBetList({GroupBetId:item.Id}).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          this.DetailData.tableData = obj
          if (obj.length>0){
            this.DetailData.GroupUserName = obj[0].UserName;
            this.DetailData.GroupStrPeriod =obj[0].StrPeriod;
            this.DetailData.GroupGameName = item.GameName;
            this.DetailData.GroupResultNums = obj[0].ResultNums;
          }

          resolve();
        } else {
          this.DetailData.showDetailDialog = false;
          this.$toast.error(msg);
        }
      }).catch(error => {
      });
    },
    queryData(filterItems){
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.query.userName = '';
      this.query.userId =0;
      this.query.beginDay = '';
      this.query.endDay = '';

      for (let key in filterItems) {
        if (filterItems[key].Name === 'userName') {
          this.query.userName = filterItems[key].Value;
        } else if (filterItems[key].Name === 'gameType') {
          this.query.gameType = filterItems[key].Value;
        } else if (filterItems[key].Name === 'state') {
          this.query.state = filterItems[key].Value;
        } else if (filterItems[key].Name === 'userType') {
          this.query.userType = filterItems[key].Value;
        } else if (filterItems[key].Name === 'beginDay') {
          this.query.beginDay = filterItems[key].Value;
        } else if (filterItems[key].Name === 'endDay') {
          this.query.endDay = filterItems[key].Value;
        }else if (filterItems[key].Name === 'GroupBetSn') {
          this.query.GroupBetSn = filterItems[key].Value;
        }else if (filterItems[key].Name === 'Period') {
          this.query.Period = filterItems[key].Value;
        }
      }

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
      });
    },
  },
  created() {
    this.query.pageIndex = 1;
    this.query.FirstId = 0;
    this.getData((v)=>{
      if (v==true){
        this.query.pageIndex = this.query.pageIndex+1
      }
    });
  },
}
</script>

<style scoped>
@import url('../../assets/css/lottery_num.css');
@import url('../../assets/css/main.css');

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #F2F2F2;
  justify-content: space-between;
  padding: 5px 11px;
  margin: 0 0 10px;
  background: #fff;
}
.desc {
  color: #8B8B8B;
  padding: 10px;
  text-align: left;
  font-size: 12px;
  width: 100%;
}
.title {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.title_l {
  /*font-weight: bold;*/
  color: black;
  /*height: 20px;*/
  overflow: hidden;
  font-size: 14px;
}
.price {
  color: red;
  font-size: 16px;
  font-weight: 900;
}
.fsid {
  color: #8B8B8B;
}

.priceDiv {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.xiaol {
  color: #8B8B8B;
  color: red;
}
.kc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.btn_01 {
  border: 1px solid #8B8B8B;
  border-radius: 3px;
  text-align: center;
  margin-left: 10px;
  padding: 3px 8px;
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}

</style>



