import {request} from "../utils/http";

export function GetGroupBetListFirstId(query) {
  return request({ url: 'api/getgroupbetlistfirstid', method: 'post', data: query });
}
export function GetGroupDetailBetList(query) {
  return request({ url: 'api/getgroupdetailbetlist', method: 'post', data: query });
}

export function Get7DayBetYieldChart(query) {
  return request({ url: 'api/get7daybetyieldchart', method: 'post', data: query });
}
export function GetMainData(query) {
  return request({ url: 'api/getmaindata', method: 'post', data: query });
}




